.wrapper-item{margin-bottom:20px;padding-bottom:10px;border-bottom:1px solid #dce5eb}.wrapper-item:last-of-type{margin-bottom:0}.wrapper-item>h2{margin-bottom:10px;font-size:16px;line-height:20px;font-weight:500}.item-detail{display:flex;margin-top:10px;margin-bottom:10px}.item-detail .item-name{margin-right:10px;width:140px;text-align:right;color:#6f7f89}.item-detail .item-name.with-input{line-height:40px}.item-detail .item-desc{color:#21333f}.item-encrypt{padding-top:10px;padding-bottom:10px;width:80%;border-bottom:1px solid #dce5eb}.item-encrypt:last-of-type{border-bottom:none}.item-encrypt .item-title{margin-bottom:10px;font-size:14px;font-weight:700}.item-encrypt .item-detail{margin-bottom:10px;width:100%}.item-encrypt .item-detail:last-child{margin-bottom:0}.item-encrypt .item-detail .item-name{width:100px}.item-encrypt .item-detail .item-desc{width:700px;word-break:break-all}
